function getToken() {
    const tokenString = sessionStorage.getItem('token');
    const tokenObject = JSON.parse(tokenString);
    
    return tokenObject;
}

function Segura() {
    const token = getToken();




  if(!token){
    return (
      <div>
        <Login></Login>
      </div>
    );
    }

    const fechaActual = Date.now();
    const tiempoTranscurrido = fechaActual - token.fecha;

    if(tiempoTranscurrido > 30000){
      return (
        <div>
          <Login></Login>
        </div>
      );
      }

    return(
        <div style={estilos.seguridad}>
            <h1>PAGINA SEGURA</h1>
            <DataTable
            columns={columnas}
            data={tablaInversion}
            title="Posibles Inversionistas"
            />
        </div>
    )
}

const estilos={
  seguridad: {backgroundColor: "purple",
},
}
export default Segura;