
import img2 from '../images/gn2.jpeg';
import img3 from '../images/gn3.jpeg';
import img4 from '../images/gn4.jpeg';
import img5 from '../images/gn5.jpeg';
import img6 from '../images/gn6.jpeg';
import React, { useState } from 'react';
import {
  Carousel,
  CarouselItem,
  CarouselControl,
  CarouselIndicators,
  CarouselCaption
} from 'reactstrap';
import 'bootstrap/dist/css/bootstrap.min.css';


const items = [
  
  {
    src: img2,
    altText: 'Foto 2',
    
  },
  {
    src: img3,
    altText: 'Foto 3',
    
  },
  {
    src: img4,
    altText: 'Foto 1',
    
   
  },
  {
    src: img5,
    altText: 'Foto 2',
    
  },
  {
    src: img6,
    altText: 'Foto 3',
    
  },




];

const Carrucel = (props) => {
  const [activeIndex, setActiveIndex] = useState(0);
  const [animating, setAnimating] = useState(false);

  const next = () => {
    if (animating) return;
    const nextIndex = activeIndex === items.length - 1 ? 0 : activeIndex + 1;
    setActiveIndex(nextIndex);
  }

  const previous = () => {
    if (animating) return;
    const nextIndex = activeIndex === 0 ? items.length - 1 : activeIndex - 1;
    setActiveIndex(nextIndex);
  }

  const goToIndex = (newIndex) => {
    if (animating) return;
    setActiveIndex(newIndex);
  }

  const slides = items.map((item) => {
    return (
      <CarouselItem
        onExiting={() => setAnimating(true)}
        onExited={() => setAnimating(false)}
        key={item.src}
      >
        <img src={item.src} alt={item.altText} width="100%" height="auto" />
        <CarouselCaption captionText={item.caption} captionHeader={item.caption} />
      </CarouselItem>
    );
  });

  return (
    <Carousel
      activeIndex={activeIndex}
      next={next}
      previous={previous}
    >
      <CarouselIndicators items={items} activeIndex={activeIndex} onClickHandler={goToIndex} />
      {slides}
      <CarouselControl direction="prev" directionText="<" onClickHandler={previous} />
      <CarouselControl direction="next" directionText=">" onClickHandler={next} />
    </Carousel>
  );
}

export default Carrucel;